<template>
  <div class="pagination-table" :class="'table-' + size">
    <el-table
      :data="list"
      stripe
      :size="size"
      v-loading="tableLoading"
      style="width: 100%">
      <el-table-column label="序号" align="center" type="index" :index="dataIndex"></el-table-column>

      <slot></slot>

    </el-table>

    <div class="pagination-table-pgn">
      <el-pagination
        background
        layout="prev, pager, next, sizes"
        :page-sizes="[10,20,50]"
        :page-size="pageSize"
        :current-page="pageNum"
        :total="total"
        @size-change="onSizeChange"
        @current-change="onCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'pagination-table',
  props: {
    apiName: String,
    data: Object,
    options: Object,
    sucFn: Function,
    errFn: Function,
    beforeRender: Function,
    type: { // 类型。 可选值：page / popup
      type: String,
      default: 'page'
    },
    size: {
      type: String,
      default: 'medium'
    }
  },
  data () {
    return {
      tableLoading: false, // 表格是否加载中
      list: [], // 表格数据
      pageNum: 1, // 当前页
      pageSize: 10, // 每页显示数据行
      total: 0 // 总数据数
    }
  },
  watch: {
    // 在第三级菜单完成操作后返回上一页，重新获取数据，以保证数据的更新
    $route (val, oldVal) {
      if (val.matched.length <= 2 && oldVal.matched.length > 2) {
        this.getPageList()
      } else {
        this.changePathHash('replace')
      }
    }
  },
  created () {
    const query = this.$route.query
    this.pageNum = Number(query.pageNum || 1)
    this.pageSize = Number(query.pageSize || 10)
    this.getPageList()
    this.changePathHash('replace')
  },
  methods: {
    getPageList () {
      this.tableLoading = true
      this.$http[this.apiName](Object.assign({}, {
        pageNum: this.pageNum,
        pageSize: this.pageSize
      }, this.data), this.options)
        .then(res => {
          this.pageNum = res.pageNum
          this.pageSize = res.pageSize
          this.total = res.total
          this.list = this.beforeRender ? this.beforeRender(res.list) : res.list
          this.sucFn && this.sucFn(this.list)
        })
        .catch(error => {
          this.errFn(error)
        })
        .finally(() => {
          this.tableLoading = false
        })
    },

    onCurrentChange (pageNum) {
      this.pageNum = pageNum
      this.getPageList()
      this.changePathHash()
    },

    onSizeChange (pageSize) {
      this.pageSize = pageSize
      this.reset()
    },

    reset () {
      this.onCurrentChange(1)
    },

    // 切换页面时更新hash路径
    changePathHash (type = 'push') {
      // 弹窗不用更新hash
      if (this.type === 'popup') return

      // 避免重复跳转
      const query = this.$route.query
      if (this.pageNum === Number(query.pageNum) && this.pageSize === Number(query.pageSize)) return

      this.$router[type]({
        path: this.$route.path,
        query: Object.assign({}, this.$route.query, {
          pageNum: this.pageNum,
          pageSize: this.pageSize
        })
      })
    },

    // 表格数据序号
    dataIndex (index) {
      return (this.pageNum - 1) * this.pageSize + index + 1
    }
  }
}
</script>

<style lang="scss" scoped>
.pagination-table {
  &.table-small {
    ::v-deep .el-link {
      font-size: 12px;
    }
  }

  .pagination-table-pgn {
    padding: 30px 0;
    text-align: right;

    .el-pagination {
      padding: 0;
      font-weight: normal;

      ::v-deep .el-pagination__sizes {
        margin-right: -5px;
      }
    }
  }
}
</style>
