<template>
  <el-dialog
    class="popup-use-imprest-records"
    title="备用额度使用申请记录"
    width="80%"
    :visible.sync="show"
    destroy-on-close
    @closed="onClose"
  >
    <el-new-table :apiName="apiName" :data="{ id }" type="popup">
      <el-table-column prop="createTime" label="申请时间" align="center" min-width="150">
        <template slot-scope="scope">{{ scope.row.createTime | fmtDateTime }}</template>
      </el-table-column>
      <el-table-column prop="budgetAmount" label="申请时预算额度" align="center" min-width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.budgetAmount | fmtMoney }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="usedAmount" label="申请时已用额度" align="center" min-width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.usedAmount | fmtMoney }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="imprestAmount" label="申请时备用额度" align="center" min-width="120">
        <template slot-scope="scope">
          <span>{{ scope.row.imprestAmount | fmtMoney }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="creater" label="申请人" align="center" min-width="120"></el-table-column>
      <el-table-column prop="status" label="申请状态" align="center">
        <template slot-scope="scope">
          <appl-sts-icon :status="scope.row.status" />
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" min-width="120">
        <template slot-scope="scope">
          <el-link type="primary" icon="el-icon-s-order" :underline="false" @click="popupDtl(scope.row.id)">查看详情</el-link>
        </template>
      </el-table-column>
    </el-new-table>

    <el-divider></el-divider>

    <div class="dialog-footer">
      <el-button size="small" plain @click="show = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
import ElNewTable from '@/components/ElNewTable'
import ApplStsIcon from '@/components/status/ApplStsIcon'

export default {
  name: 'popup-use-imprest-records',
  components: {
    ElNewTable,
    ApplStsIcon
  },
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    visible: Boolean,
    id: [Number, String]
  },
  data () {
    return {
      apiName: 'serviceGetUseImpRecord',
      show: false
    }
  },
  watch: {
    visible (val) {
      this.show = val
    }
  },
  methods: {
    onClose () {
      this.$emit('update:visible', this.show)
    },

    popupDtl (id) {
      this.$emit('popupDtl', id)
    }
  }
}
</script>

<style lang="scss" scoped>
.popup-use-imprest-records {
  .el-link,
  .el-table {
    font-size: 13px;
  }

  .dialog-footer {
    text-align: center;

    .el-button {
      min-width: 100px;
    }
  }
}
</style>
