<template>
  <span class="sts-icon" :class="status | fmtStatusColor" :style="{ marginLeft: point ? '20px' : 0 }">
    <i v-if="point && status !== undefined"></i>{{ status | fmtStatus }}
  </span>
</template>

<script>
const STATUS = {
  0: '已完成',
  1: '申请中',
  2: '已编辑',
  3: '已取消',
  4: '未通过'
}

export default {
  name: 'appl-sts-icon',
  props: {
    status: [Number, String],
    point: Boolean
  },
  filters: {
    // 状态文案
    fmtStatus (val) {
      return val === undefined ? '' : (STATUS[val] ? STATUS[val] : '未知')
    },

    // 状态颜色
    fmtStatusColor (val, type = 'color') {
      switch (STATUS[val]) {
        case '已完成': return 'color-green'
        case '申请中':
        case '已编辑': return 'color-orange'
        case '已取消': return 'color-black'
        case '未通过': return 'color-red'
        default: return 'color-black'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import './stsIcon.scss';
</style>
